import { Elements } from '../views/elements';

export default class Operations {
  constructor() {
    this.operations = [];
  }

  getOperations() {
    if (JSON.parse(localStorage.getItem('operations')) !== null) {
      this.operations = JSON.parse(localStorage.getItem('operations'));
    }
  }

  updateOperations() {
    this.operations = [];
    Elements.operations.forEach((operation, i) => {
      if (operation.checked) {
        this.operations.push(operation.value);
      }
    });
    localStorage.setItem('operations', JSON.stringify(this.operations));
    console.log(JSON.parse(localStorage.getItem('operations')));
  }
}